import type { JsonRpc } from './index'
import type { KycProvider, UserInfo, WaKey } from './types';


/**
 * Checks blockchain for a list of kycproviders, and sorts through the ones that are blacklisted.
 * @returns An array of strings with valid kyc provider names.
 */
 export async function getValidKycProviders(this: JsonRpc) {
    try {
      const { rows } = await this.get_table_rows({
        code: 'eosio.proton',
        table: 'kycproviders',
        scope: 'eosio.proton',
      });
      this.validKycProviders = rows.reduce((acc: string[], provider: KycProvider) => {
        if (!provider.blisted) {
          acc.push(provider.kyc_provider)
        }

        return acc
      }, [])
    } catch (e) {
      throw new Error('Unable to get KYC Providers.')
    }
  }

/**
 * Takes an account or a list of accounts and checks to make sure that they are light-verified
 * @param account This could be an array of user rows from the usersinfo table on the blockchain, or a singular string for an account name
 * @returns An object of the user data as pulled from the usersinfo table on the blockchain with an additional key, isLightKYCVerified (boolean)
 */
export async function isLightKYCVerified(
  this: JsonRpc,
  account: UserInfo[] | string
) {
    const lightKyc = [
      "firstname",
      "lastname",
      "birthdate",
      "address"
    ];

    if (account.length === 0) {
      throw new Error('Please enter an account.');
    }

    if (this.validKycProviders.length === 0) {
      await this.getValidKycProviders();
    }

    let users: UserInfo[] = [];

    if (Array.isArray(account)) {
      users = account;
    } else if (typeof account === 'string') {
      try {
        const { rows } = await this.get_table_rows({
            code: 'eosio.proton',
            table: 'usersinfo',
            scope: 'eosio.proton',
            lower_bound: account,
            upper_bound: account,
        });
        
        users = rows;
      } catch(e) {
        throw new Error('Account not found!');
      }
    }
    
    const resultsWithKycStatus: UserInfo[] = users.map((user: UserInfo) => {
      const levelsResult: string[] = user.kyc.reduce((acc: string[], kyc) => {
        if (this.validKycProviders.indexOf(kyc.kyc_provider) >= 0) {
          const result = kyc.kyc_level.split(',').map((kycItem: string) => kycItem.split(':')[1]);
          acc = result.concat(acc);
        }

        return acc
      }, []);

      user.isLightKYCVerified = lightKyc.every(lightKycField => levelsResult.includes(lightKycField));

      return user
    })

    return resultsWithKycStatus;
};

export async function fetchCredentials(
  this: JsonRpc,
  actor: string
): Promise<WaKey[]> {
  const { rows: credentials }: { rows: WaKey[] } = await this.get_table_rows({
    code: 'webauthn',
    scope: 'webauthn',
    table: 'credentials',
    lower_bound: actor,
    upper_bound: actor,
    key_type: 'name',
    index_position: 2,
  });
  return credentials;
};
  